/**
 * WebSocket客户端
 *
 * @author mjy
 * @date 2019-04-21
 */

import packetCode from './PacketCodeC'

let client
let eventDispatcher

const EventDispatcher = function () {
  this.listeners = {}
}

const socket = {
  close: function () {
    if (client !== undefined && client.readyState === WebSocket.OPEN) {
      client.close()
    }
  },
  open: function () {
    // 构造方法
    eventDispatcher = new EventDispatcher()

    // 参数就是与服务器连接的地址
    client = new WebSocket('ws://47.105.74.119:9999/chat')

    // 指定接收二进制数据类型
    client.binaryType = 'arraybuffer'

    // 接收到消息
    client.onmessage = function (event) {
      // 解码
      let responsePacket = packetCode.decode(event.data)

      // 显示指令，只是用于调试
      showCommand(responsePacket.command)

      // 派发接收数据事件
      eventDispatcher.dispatchEvent(responsePacket.command, toJSON(responsePacket))
    }

    // 连接建立
    client.onopen = function (event) {
    }

    // 连接关闭
    client.onclose = function (event) {
    }

    // 连接发生错误
    client.onerror = function (event) {
    }
  },
  /**
   * 登录
   * @param username 用户名
   * @param password 密码
   */
  login: (username, password, func) => {
    let requestPacket = {
      username: username,
      password: password,
      version: 1,
      command: 1
    }
    send(requestPacket)
    eventDispatcher.addListener('2', func)
  },

  /**
   * 一对一发送消息
   * @param toUserId 接收用户的userId
   * @param message 消息内容
   */
  sendToFriend: (toUserId, userId, message, msgType, func) => {
    let requestPacket = {
      userId: userId,
      toUserId: toUserId,
      msgType: msgType,
      message: message,
      version: 1,
      command: 3
    }
    send(requestPacket)
    eventDispatcher.addListener('4', func)
  },

  /**
   * 退出登录
   */
  logout: (func) => {
    let requestPacket = {
      version: 1,
      command: 5
    }
    send(requestPacket)
    eventDispatcher.addListener('6', func)
  },

  /**
   * 创建群组
   * @param userIdList 用户userId列表，多个以英文,分割
   */
  createGroup: (userIdList) => {
    let requestPacket = {
      userIdList: userIdList.split(','),
      version: 1,
      command: 7
    }
    send(requestPacket)
  },

  /**
   * 群组内成员列表
   * @param groupId 群组ID
   */
  getGroupMembers: (groupId, userId, func) => {
    let requestPacket = {
      groupId: groupId,
      userId: userId,
      version: 1,
      command: 9
    }
    send(requestPacket)
    eventDispatcher.addListener('10', func)
  },

  /**
   * 加入群组
   * @param groupId 群组ID
   */
  joinGroup: (groupId) => {
    let requestPacket = {
      groupId: groupId,
      version: 1,
      command: 11
    }
    send(requestPacket)
  },

  /**
   * 退出群组
   * @param groupId
   */
  quitGroup: (groupId) => {
    let requestPacket = {
      groupId: groupId,
      version: 1,
      command: 13
    }
    send(requestPacket)
  },

  getGroups: (condition, userId, func) => {
    let requestPacket = {
      condition: condition,
      userId: userId,
      version: 1,
      command: 25
    }
    send(requestPacket)
    eventDispatcher.addListener('26', func)
  },

  /**
   * 发送消息
   * @param toGroupId
   * @param userId
   * @param message
   * @param msgType
   * @param func
   */
  sendToGroup: (toGroupId, userId, message, msgType, func) => {
    let requestPacket = {
      toGroupId: toGroupId,
      userId: userId,
      msgType: msgType,
      message: message,
      version: 1,
      command: 15
    }
    send(requestPacket)
    eventDispatcher.addListener('16', func)
  },
  getFriendMessages: (toUserId, userId, pageNum, func) => {
    let requestPacket = {
      userId: userId,
      pageNum: pageNum,
      toUserId: toUserId,
      pageSize: 5,
      version: 1,
      command: 27
    }
    send(requestPacket)
    eventDispatcher.addListener('28', func)
  },
  getGroupMessages: (toGroupId, userId, pageNum, func) => {
    let requestPacket = {
      toGroupId: toGroupId,
      pageNum: pageNum,
      pageSize: 5,
      userId: userId,
      version: 1,
      command: 23
    }
    send(requestPacket)
    eventDispatcher.addListener('24', func)
  },

  /**
   * 聊天列表
   */
  chats: (userId, func) => {
    let requestPacket = {
      userId: userId,
      version: 1,
      command: 19
    }
    send(requestPacket)
    eventDispatcher.addListener('20', func)
  },
  /**
   * 更改用户信息
   * @param userId
   * @param avatar
   * @param nickName
   * @param func
   */
  updateUser: (userId, avatar, nickName, func) => {
    let requestPacket = {
      userId: userId,
      avatar: avatar,
      nickName: nickName,
      version: 1,
      command: 21
    }
    send(requestPacket)
    eventDispatcher.addListener('22', func)
  },
  openChat: (chatId, userId, chatType, func) => {
    let requestPacket = {
      userId: userId,
      chatId: chatId,
      chatType: chatType,
      version: 1,
      command: 39
    }
    send(requestPacket)
    eventDispatcher.addListener('40', func)
  },
  getMoneys (userId, pageNum, func) {
    let requestPacket = {
      pageNum: pageNum,
      pageSiz: 5,
      userId: userId,
      version: 1,
      command: 41
    }
    send(requestPacket)
    eventDispatcher.addListener('42', func)
  }
}

function toJSON (responPacket) {
  return JSON.parse(JSON.stringify(responPacket))
}

/**
 * 添加监听器
 * @param eventKey
 * @param fun
 * @param context
 * @returns {{func: *, context: *}}
 */
EventDispatcher.prototype.addListener = function (eventKey, fun, context) {
  let list = []
  this.listeners[eventKey] = list
  let listener = {
    func: fun,
    context: context
  }
  list.push(listener)
  return listener
}

/**
 * 移除监听器
 * @param eventKey
 * @param fun
 * @param context
 */
EventDispatcher.prototype.removeListener = function (eventKey, fun, context) {
  let list = this.listeners[eventKey]
  if (list !== undefined) {
    let size = list.length
    for (let i = 0; i < size; i++) {
      let listener = list[i]
      if (listener.func === fun && listener.context === context) {
        list.splice(i, 1)
        return
      }
    }
  }
}

/**
 * 给监听器派发事件
 * @param eventKey
 * @param event
 */
EventDispatcher.prototype.dispatchEvent = function (eventKey, event) {
  let list = this.listeners[eventKey]
  if (list !== undefined) {
    let size = list.length
    for (let i = 0; i < size; i++) {
      let listener = list[i]
      let fun = listener.func
      let context = listener.context
      if (context != null) {
        fun.call(context, event)
      } else {
        fun(event)
      }
    }
  }
}
/**
 * 命令
 * @param command
 */
let showCommand = (command) => {
  let funStr = ''
  switch (command) {
    case 2:
      funStr = '登录'
      break
    case 4:
      funStr = '一对一消息'
      break
    case 6:
      funStr = '退出登录'
      break
    case 8:
      funStr = '创建群组'
      break
    case 10:
      funStr = '群组成员列表'
      break
    case 12:
      funStr = '加入群组'
      break
    case 14:
      funStr = '退出群组'
      break
    case 16:
      funStr = '群消息'
      break
    case 18:
      funStr = '心跳检测回应'
      break
    case 20:
      funStr = '聊天列表'
      break
    case 24:
      funStr = '聊天记录'
      break
    case 26:
      funStr = '群列表'
      break
    default:
      break
  }
  console.log('指令 = ' + command + ', 指令功能 = ' + funStr)
}

/**
 * 发送
 * @param packet
 */
let send = (requestPacket) => {
  if (!window.WebSocket) {
    alert('当前浏览器不支持WebSocket')
    return
  }
  // 当websocket状态打开
  if (client !== undefined && client.readyState === WebSocket.OPEN) {
    client.send(packetCode.encode(requestPacket))
  } else {
    socket.open()
  }
}
export default socket
