<template>
  <div class="home">
    <mt-header fixed :title="$route.name">
      <mt-button slot="left" @click="$router.go(-1)" icon="back">返回</mt-button>
      <mt-button icon="more" slot="right"></mt-button>
    </mt-header>
    <router-view></router-view>
    <mt-tabbar v-show="isShow" :fixed="true" v-model="active">
      <mt-tab-item :id="i" v-for="(item, i) in tabList" :key="i" @click.native="$router.push(item.path)">
        <i slot="icon" :class="`iconfont ${item.icon}`"></i>{{item.name}}
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>
<script>
import './common.less'
export default {
  name: 'Home',
  data () {
    return {
      active: 0,
      path: ['/chatList', '/groupList', '/mine'],
      tabList: [
        { icon: 'icon-conversation_icon', path: '/chatList', name: '聊天', index: 0 },
        { icon: 'icon-qunliao', path: '/groupList', name: '群聊', index: 1 },
        { icon: 'icon-wode', path: '/mine', name: '我的', index: 2 }
      ]
    }
  },
  created () {
    this.getActive()
  },
  computed: {
    isShow () {
      return this.path.includes(this.$route.path)
    }
  },
  methods: {
    getActive () {
      if (this.path.includes(this.$route.path)) {
        this.active = this.tabList.filter(v => v.name === this.$route.name)[0].index
      }
    }
  }
}
</script>
<style lang="less">
  .home {
    padding-top: 40px;
    padding-bottom: 100px;
    .mint-tabbar .is-selected {
      background: none;
    }
    .mint-tab-item-icon {
      display: flex;
      align-items: center;
      justify-content: center;
      i {
        font-size: 20px;
      }
    }
  }
</style>
